.button-primary {
  position: relative;
  overflow: hidden;
  padding: 15px 30px;
  border: none;
  border-radius: 49.5px;
  background-color: #FFC80A;
  text-transform: uppercase;
  font-family: 'Montserrat';
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 2px;
  color: #000;
  outline: none;
  cursor: pointer;
  -webkit-transition: background-color 0.25s ease-in, color 0.5s ease-in;
  transition: background-color 0.25s ease-in, color 0.5s ease-in;
}
